
import { useEffect, useState } from 'react'
import todoService from './services/todos'

import {
  Container,
  Divider,
  Typography,
  Box,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  TextField,
  Button
} from '@mui/material'

import CheckIcon from '@mui/icons-material/Check'
import CloseIcon from '@mui/icons-material/Close'



function App () {
  
  const [ todos, setTodos ] = useState([ { todo: 'You need to do something', done: true } ])
  const [ todoText, setTodoText] = useState('')
  
  
  useEffect(() => {
    
  const getTodos = async () => {
     const allTodos = await todoService.getTodos()
      setTodos(allTodos)
  }
    getTodos() 
  }, [])
  
  const handlechange = (event) => {
       
   
    
      setTodoText(event.target.value)
    
    
  }

  return (
    <Container >
      
      <Box sx={{ width: '100%', maxWidth: 900, minHeight: 700, height: 'contentFit', bgcolor: 'background.paper', backgroundImage: 'url(image.jpg)' }}>
        <ListItem>
          <Typography> <h3>Tasks</h3> </Typography>        
      </ListItem>
      <Divider/>
      {
        todos.map(item => {
          return (
      <nav key={item.todo}>
        <List>                  
          <ListItem disablePadding>
            <ListItemButton>
              <ListItemIcon>
                {item.checked ? <CheckIcon color='primary'/> : <CloseIcon color='error'/>}
              </ListItemIcon>
              <ListItemText primary={item.todo}/>
            </ListItemButton>
          </ListItem>          
        </List>
      </nav>  
          )
        })
        }
        <Divider/>
      </Box>
      <form style={{maxWidth: 900} }>
          <TextField
           fullWidth
            id="outlined-basic"
            label="New note"
            variant="standard"            
            onChange={handlechange}
            helperText={todoText.length <= 140
              ? `${140 - (todoText.length)} characters left`
              : 'too long!'}
            value={todoText}
            
          />
        <Button variant="contained" disabled={todoText.length > 140 }>Save</Button>
        </form>
     
      
    </Container>
  );
}

export default App;
